/* Resets */
body, 
body * {
    margin: 0;
    padding: 0;
    text-decoration: none;

    font-family: system-ui;
    font-size: 16px;
    line-height: 1.6em;
    color: #2e2e2e;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-size: 1.5em;
    }

    p {
        margin: 1em 0;
    }

    a {
        color: rgb(91, 91, 250);
    }

    a:hover {
        color: rgb(0, 0, 0);
    }

    li {
        padding-top: 0.2em;
    }

    span {
        color: grey;
    }
}

/* Container */
.container {
    width: 90%;
    max-width: 800px;
    margin-right: auto;
    margin-left: auto;
    margin-top: 1em;
}

/* Header */
.main-nav {
    padding-bottom: 0.5em;
    border-bottom: 0.1rem solid #e8e8e8;
    margin-bottom: 1em;
    a {
        color: black;
        font-weight: bold;
    }
}

/* Index */
.posts-nav {
    li {
        list-style: none;
    }
    p a {
        font-weight: bold;
    }
    p {
        margin: 0;
    }
}

/* Blog post */
.blog-post {
    img {
        width: 100%;
    }
    
    figcaption {
        font-size: 0.7em;
        text-align: center;
    }

    li {
        margin-left: 2em;
    }
}

/* Footer */
.footer-contact {
    display: flex;
    padding-top: 0.5em;
    border-top: 0.1em solid #e8e8e8;
    margin-top: 4em;
    justify-content: center;
}

/* 404 */
.not-found {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h1 {
        font-size: 2em;
    }
}